@import '../../common/styles/sass/variables';

.project {
    width: 95%;
    margin-bottom: 1rem;
    display: inline-block;
    &:nth-child(4n+1) .img, &:nth-child(4n) .img {
        height: 350px;
    }
    .img {
        height: 250px;
        background-position: center;
        background-size: cover;
        .overlay {
            height: 100%;
            width: 100%;
            background-color: rgba(0, 0, 0, 0.5);
            opacity: 0;
            transition: opacity .25s ease-in-out;
            &:hover {
                opacity: 1;
            }
            .description {
                padding: 1rem;
                font-family: $mainFontFamily;
                h3 {
                    text-transform: uppercase;
                    font-weight: 800;
                    font-size: 1.25rem;
                    color: $bgButtonColor;
                }
                p {
                    color: $secondElementsTextColor;
                    margin-bottom: 1.5rem;
                }
                .links{
                    display: flex;
                    flex-direction: column;
                    .btnDemo,.btnCode {
                        margin-bottom: 0.3rem;
                        text-decoration: none;
                        border: 1px solid $secondElementsTextColor;
                        outline: 0;
                        transition: .25s;
                        text-transform: uppercase;
                        padding: 0.4rem 0.75rem;
                        cursor: pointer;
                        color: $secondElementsTextColor;
                        &:hover {
                            background-color: #2b2b2b;
                        }
                }

                }
            }
        }

    }
}
